* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Hind", sans-serif;
}

@import url("https://fonts.googleapis.com/css2?family=Hind&family=Poppins:wght@300&display=swap");

.header {
  background-color: #202124;
  color: white;
  padding: 10px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #525355;
  position: fixed;
  width: 100%;
  top: 0;
}
#search-bar {
  width: 50%;
  padding: 15px;
  border: none;
  background-color: #525355;
  border-radius: 5px;
  color: white;
  font-size: 16px;
  outline: none;
}

#search-bar::placeholder {
  color: white;
}
.right-menu {
  display: flex;
  gap: 15px;
}
.reload-icon,
.view-list-icon,
.setting-icon,
.menu-icon {
  cursor: pointer;
}
.logo-slogan {
  font-size: 24px;
  text-decoration: underline;
}
.logo-content {
  display: flex;
  align-items: center;
}

/* Body Css */

.container {
  background-color: #202124;
  min-height: 100vh;
  padding: 100px 20px;
  display: flex;
  flex-wrap: wrap;
}

.left-menu li {
  font-size: 18px;
  color: white;
}

.left-menu li a {
  text-decoration: none;
  color: white;
}
.single-menu {
  display: flex;
  align-items: center;
  gap: 30px;
  color: white;
  padding: 15px 20px;
  border-radius: 40px;
  cursor: pointer;
  margin: 5px 0px;
}
.single-menu li {
  align-items: center;
  display: flex;
  justify-content: center;
}
.left-menu .active {
  background-color: #41331c;
}
.single-menu:hover {
  background-color: #28292c;
  color: white;
}
/* Input Box */

.wrap {
  width: 500px;
  padding: 20px;
  background-color: transparent;
  border: 1px solid #525355;
  border-radius: 10px;
  margin: 0 auto;
}
.wrap input,
textarea {
  border: none;
  background-color: transparent;
  outline: none;
  color: white;
  margin-bottom: 20px;
  width: 100%;
  font-size: 16px;
}
.wrap input::placeholder,
textarea::placeholder {
  color: #a6a6a7;
}
.editing-icons {
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.editing-icons:nth-child(n) {
  cursor: pointer;
}
.addBtn,
.closeBtn {
  padding: 10px 20px;
  border: none;
  outline: none;
  background-color: #25262a;
  color: white;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
}
.addBtn:hover,
.closeBtn:hover {
  background-color: white;
  color: black;
}

/* Note adding */

.note-wrap {
  width: 250px;
  padding: 20px;
  background-color: transparent;
  border: 1px solid #525355;
  border-radius: 10px;
  margin-top: 20px;
}
.note-wrap h1 {
  font-size: 24px;
  color: white;
  margin-bottom: 20px;
}
.note-wrap p {
  font-size: 16px;
  color: white;
  margin-bottom: 40px;
}

.main-wrap {
  width: 80%;
  padding: 0px 0px 0px 80px;
}
.wrap-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  align-items: center;
}
